  justify-content: space-between;
  align-items: center;
  padding: 0 12%;
  padding-top: 50px;
  padding-bottom: 50px;
  background-color: #b8dcfb;
  background-image: url("../img/booking-bg-desktop@1x.jpg");
  background-repeat: no-repeat;
  background-size: cover; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .booking {
      background-image: url("../img/booking-bg-desktop@2x.jpg"); } }

.booking h2 {
  margin: 0;
  margin-top: 11px;
  font-weight: 700;
  font-size: 40px;
  line-height: 60px;
  color: #011c40; }

.booking h2 + p {
  width: 365px;
  margin-top: 5px; }

.booking p {
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
  color: #011c40; }

.booking__form {
  display: flex;
  flex-direction: column;
  width: 403px;
  min-height: 455px;
  box-sizing: border-box;
  padding: 50px;
  padding-top: 25px;
  background-color: #011c40;
  box-shadow: 0 4px 45px rgba(1, 28, 64, 0.15); }

.booking__form p {
  color: #f9fbfd;
  margin-bottom: 26px; }

.booking__form input {
  width: 100%;
  max-width: 303px;
  height: 43px;
  box-sizing: border-box;
  margin-bottom: 20px;
  padding: 15px;
  border: none; }
  .booking__form input:hover, .booking__form input:focus {
    background-color: #ededed; }

.booking__form input::placeholder {
  font-size: 14px;
  line-height: 21px;
  color: #011c40; }

.booking__form input[type="checkbox"] + label {
  position: relative;
  min-height: 21px;
  box-sizing: border-box;
  margin-top: 0;
  padding-left: 23px;
  font-size: 10px;
  line-height: 21px;
  color: #f9fbfd;
  cursor: pointer; }
  .booking__form input[type="checkbox"] + label:hover, .booking__form input[type="checkbox"] + label:focus {
    color: #ededed; }

.booking__form input[type="checkbox"] + label::before {
  content: "";
  display: block;
  position: absolute;
  width: 18px;
  height: 18px;
  background-color: #ffffff;
  top: 0;
  left: 0;
  box-sizing: border-box; }

.booking__form input[type="checkbox"]:hover + label::before,
.booking__form input[type="checkbox"]:focus + label::before {
  background-color: #d4d4d4; }

.booking__form input[type="checkbox"]:checked + label::after {
  position: absolute;
  content: "";
  top: 3px;
  left: 4px;
  z-index: 5;
  width: 9px;
  height: 7px;
  transform: rotate(-45deg);
  border-left: 1px solid #101a56;
  border-bottom: 1px solid #101a56; }

.booking__form button {
  width: 100%;
  height: 43px;
  margin-top: 29px;
  font-size: 14px;
  background-color: #f9fbfd;
  border: none;
  cursor: pointer; }
  .booking__form button:hover, .booking__form button:focus {
    background-color: #ededed; }

@media (max-width: 1023px) {
  .booking {
    padding: 0 5.7%;
    padding-top: 74px;
    padding-bottom: 100px;
    flex-direction: column;
    text-align: center;
    background-image: url("../img/booking-bg-tablet@1x.jpg"); } }
  @media (max-width: 1023px) and (min-resolution: 144dpi), (max-width: 1023px) and (min-resolution: 1.5dppx) {
    .booking {
      background-image: url("../img/booking-bg-tablet@2x.jpg"); } }

@media (max-width: 1023px) {
  .booking h2 {
    margin-top: 16px;
    font-size: 34px;
    line-height: 51px; }
  .booking h2 + p {
    width: 363px;
    margin: auto;
    margin-top: 5px;
    text-align: center; }
  .booking__form {
    margin-top: 50px; }
  .booking__form p {
    text-align: left; } }

@media (max-width: 767px) {
  .booking {
    padding: 0 4.7%;
    padding-top: 50px;
    padding-bottom: 65px;
    background-image: url("../img/booking-bg-mobile@1x.jpg"); } }
  @media (max-width: 767px) and (min-resolution: 144dpi), (max-width: 767px) and (min-resolution: 1.5dppx) {
    .booking {
      background-image: url("../img/booking-bg-mobile@2x.jpg"); } }

@media (max-width: 767px) {
  .booking br {
    display: none; }
  .booking div {
    width: 100%; }
  .booking h2 {
    margin-top: 9px;
    font-size: 24px;
    line-height: 31px; }
  .booking h2 + p {
    width: 72%;
    margin-top: 15px;
    font-size: 16px;
    line-height: 22px; }
  .booking__form {
    width: 100%;
    min-height: 460px;
    margin-top: 33px;
    padding: 40px 30px;
    padding-top: 15px; }
  .booking__form p {
    width: 85%;
    margin: auto;
    margin-top: 20px;
    margin-bottom: 28px;
    font-size: 18px;
    line-height: 25px;
    text-align: center; }
  .booking__form input[type="checkbox"] + label {
    text-align: left;
    line-height: 12px;
    margin-top: -2px;
    padding-left: 27px;
    padding-right: 50px; }
  .booking__form input[type="checkbox"] + label::before {
    top: 3px; }
  .booking__form input[type="checkbox"]:checked + label::after {
    top: 6px; }
  .booking__form button {
    margin-top: 28px; } }

.contacts {
  max-width: 1160px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 7.5%;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.contacts__map-section {
  position: relative; }

.contacts__contacts-section {
  padding-left: 100px; }

.contacts p {
  margin-top: -10px;
  margin-bottom: 30px;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  color: #011c40; }

.contacts address,
.contacts a {
  position: relative;
  display: block;
  padding-left: 30px;
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
  line-height: 24px;
  color: #011c40;
  text-decoration: none; }

.contacts a:hover,
.contacts a:focus {
  color: #000000;
  text-decoration: underline; }

.contacts address {
  margin-bottom: 20px; }

.contacts address::before,
.contacts a::before {
  display: block;
  content: "";
  position: absolute;
  left: 0;
  width: 30px;
  height: 30px;
  background-repeat: no-repeat; }

.contacts address::before {
  background-image: url("../img/icons/icon-map-marker.svg");
  background-size: 18px 22px; }

.contacts a::before {
  background-image: url("../img/icons/icon-phone.svg");
  background-size: 18px 18px; }

.contacts svg {
  position: absolute;
  top: 217px;
  left: 247px; }

.contacts br {
  display: none; }

@media (max-width: 1023px) {
  .contacts {
    padding: 0 5.7%; }
  .contacts img {
    width: 324px;
    height: 335px;
    object-fit: cover;
    object-position: -165px 50%; }
  .contacts svg {
    left: 108px; }
  .contacts__contacts-section {
    padding-left: 30px; }
  .contacts br {
    display: inline; } }

@media (max-width: 767px) {
  .contacts {
    padding: 0 4.7%;
    flex-direction: column-reverse; }
  .contacts__contacts-section {
    padding-left: 0; }
  .contacts p {
    padding-right: 13px; }
  .contacts__map-section {
    margin-top: 34px; }
  .contacts img {
    width: 290px;
    height: 316px;
    object-position: -145px 50%; }
  .contacts svg {
    left: 111px;
    top: 223px; } }

.page-footer {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #011c40;
  padding: 0 7.5%;
